<template>
	<div class="social-media">
		<section-header :sectionName="sectionName"></section-header>
		<div class="social-media-wrapper">
			<h1 class="yellow">LATEST NEWS, UPDATES, AND MORE</h1>
			<span>Follow our social media to stay updated</span>
			<div class="icon-bar-wrap">
				<social-media-icons :iconColor="'#fbcb17'" :size="'50px'" :isLarge="true"></social-media-icons>
			<div>
		</div>
	</div>
</template>

<script>
import SectionHeader from './SectionHeader'
import SocialMediaIcons from '../../components/SocialMediaIcons'

export default {
	name: 'social-media',
	data () {
		return {
			sectionName: 'SOCIAL MEDIA'
		}
	},
	components: {
		SectionHeader,
		SocialMediaIcons
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.social-media-wrapper{
	margin: 0 auto;
  padding: 100px 0;
	max-width: 1200px;
}
.yellow{
	color: #fbcb17;
}
.social-media{
	background-color: #353535;
	text-align: left;
}
.icon-bar-wrap{
	margin-top: 50px;
}
</style>


