<template>
	<a :href="stream.url">
		<div class="member-tile" v-bind:id="stream.display_name+'-tile'">
			<img :src="stream.logo">
			<div class="info-section">
				<h2>{{stream.display_name}}</h2>
				<p>{{description}}</p>
				<h3>{{stream.url.slice(12)}}<span v-show="stream.live"> - live</span></h3>
			</div>
		</div>
	</a>
</template>

<script>
import config from '../../../config/config.json'
export default {
	name: 'member-tile',
	computed: {
		description: function () {
			return config.team_members[this.stream.display_name.toLowerCase()].description
		}
	},
	props: ['stream']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img{
	border-radius: 100px;
	width: 100px;
	border: 2px solid #b7b7b7;
	padding: 5px;
	margin-right: 15px;
	transition: all .2s ease-in-out;
}
h2, h3{
	color: #fbcb17;
	font-size: 26px;
	margin: 0;
}
h3{
	font-size: 16px
}
p{
	margin: 7px 0;
}
.member-tile{
	display: inline-block;
	margin: 25px 70px;
	padding: 10px;
	border-radius: 10px;
	transition: all .2s ease-in-out;
}
.member-tile:hover {
	background-color: #353535;
}
#lowco2525-tile:hover img{
	border-color: #ad00df;
}
#smashley-tile:hover img{
	border-color: #3a6490;
}
#djtechlive-tile:hover img{
	border-color: #00bded;
}
#bloodyfaster-tile:hover img{
	border-color: #e13139;
}
.info-section{
	display: inline-block;
	vertical-align: top;
	padding-top: 7px;
	text-align: left;
	max-width:248px;
}
</style>


